.product__wrapper--create {
  width: 100%;
  margin-left: 80px;
}

.createHeader {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;

  border-bottom: 1px solid #2064e5;
}

.createHeader__title {
  color: #001d36;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 20px;
  align-self: flex-start;
}

.createDefaultInfo,
.createProductImage,
.createNewOption {
  display: flex;
  width: 100%;
  margin-bottom: 70px;
}

.createSection table {
  border-collapse: separate;
  border-spacing: 0 20px;
}

.createSection table {
  font-size: 12px;
  font-weight: 500;
  color: #696d78;
}

.create__optionInput input {
  width: 376px;
  height: 25px;
  margin: 0 5px 0 88px;
  border: 1px solid #cbcbcb;
  background-color: white;
  outline: none;
}

.createSection table tr {
  margin-bottom: 25px;
}

.toggle__box {
  display: flex;
  margin: 0 5px 0 88px;
  text-align: center;
  vertical-align: middle;
}
.create__sales--btn {
  width: 100px;
  height: 30px;
  background-color: white;
  border: 1px solid #cbcbcb;
  color: #cbcbcb;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 3px;
}

.create__toggle--selected {
  background-color: #2064e5;
  color: white;
}

.create__remark--textarea {
  height: 80px !important;
}

.createContainer {
  position: relative;
}

.flexible {
  display: flex;
  align-items: center;
}

.create__optionName {
  min-width: 70px;
}

.create__optionTextarea textarea {
  margin-left: 66px;
  width: 1100px;
  height: 100px;
  outline: none;
  border: 1px solid #cbcbcb;
  background-color: white;
}

.endLine {
  border-top: 1px solid #2064e5;
  margin-bottom: 30px;
}

.paddingLf10 {
  padding-left: 10px;
}

.flexible .create__optionName {
  padding-top: 10px;
}

.flexible .create__optionInput {
  margin: 0 0 0 100px;
}

.flexible .create__optionInput input {
  margin: 0;
}

.flexible .create__optionInput img {
  margin-right: 10px;
}

.not-mgbt {
  margin-bottom: 0px !important;
}

.innerTableSection {
  background-color: #f6f7f8;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

html,
body {
  width: 100%;
}

.customTr {
  width: 1100px;
}

.divTable {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 20px;
}

.toggleBox {
  display: flex;
  padding-left: 10px;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  color: #696d78;
}

.optionTable {
  border: 1px solid #cbcbcb;
  border-collapse: collapse !important;
  border-radius: 3px;
  margin-top: 20px;
  margin-right: 100px;
  margin-left: 100px;
  margin-bottom: 20px;
}

.optionTable th {
  border: 1px solid #cbcbcb;
}
.optionTable td {
  border: 1px solid #cbcbcb;
}

.optionTable thead th,
.optionTable tbody td {
  padding: 15px;
}

.optionTable input {
  height: 25px;
  border: 1px solid #cbcbcb;
  background-color: white;
  width: 150px;
  outline: none;
}

.newOptionBtn {
  width: 200px;
  height: 40px;
  background-color: #2064e5;
  color: white;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  margin-left: auto;
  margin-right: 100px;
  outline: none;
}

.optionAddButton {
  margin-left: 5px;
  background-color: #2064e5;
  color: white;
  outline: none;
  border: none;
  font-size: 12px;
  font-weight: bold;
  border-radius: 5px;
  padding: 5px;
}

.optionInput {
  margin-right: 5px;
}

.noPadding {
  padding: 0;
}

.defaultMgBottom {
  margin-bottom: 20px !important;
}

.flexibleColumn {
  display: flex;
  text-align: center;
}

.productInputMg {
  margin: 0 5px 0 88px;
}

.multipple__toggle {
  width: 150px;
  height: 30px;
  color: #9099a4;
  background-color: white;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  border: 1px solid #cbcbcb;
  margin-left: auto;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.multipple__toggle--selected {
  background-color: #2064e5 !important;
  color: white !important;
  width: 150px;
  height: 30px;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  border: 1px solid #cbcbcb;
  margin-left: auto;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.multipleCombo {
  flex-direction: row !important;
  padding: 25px 15px;
}

.selectBoxModels {
  margin-right: 10px;
}

.action__box {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 80px;
}

.createCancelBtn {
  width: 200px;
  height: 40px;
  margin: 0 10px 0 0;
  border-radius: 4px;
  border: 1px solid #1c5ae1;
  background-color: wwhite;
  color: #2064e5;
  font-size: 12px;
  font-weight: 500;
  justify-content: center;
  display: flex;
  align-items: center;
}

.createBtn {
  width: 200px;
  height: 40px;
  margin: 0 10px 0 0;
  border-radius: 4px;
  border: none;
  background-color: #2064e5;
  color: white;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
}

.thumbnail__image {
  width: 90px;
  height: 90px;
}
