.manage__header {
  background-color: #2064e5;
  display: flex;
  padding-right: 40px;
  padding-left: 65px;
  height: 64px;
  position: relative;
  align-items: center;
  z-index: 5;
}

.manage__header__column:last-child {
  margin-left: auto;
}

.manage__header__title {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  font-size: 22px;
  color: white;
}

.manage__header__default--btn {
  background-color: white;
  border-radius: 4px;
  border: none;
  font-size: 11px;
  font-weight: 500;
  color: #2064e5;
  padding: 8px 12px;
}

.manage__header__default--btn:not(:last-child) {
  margin-right: 10px;
}
