.board__view__wrapper {
  margin-left: 55px;
}

.board__category--text {
  color: #696d69;
}

.board__view--title {
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 500;
}

.board__view__header--wrap {
  display: flex;
  align-items: center;
  padding-bottom: 25px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.board__view__header--content {
  margin-left: 15px;
  color: #696d69;
  font-size: 13px;
}

.board__view__header--content--end {
  margin-left: auto;
  color: #696d69;
  font-size: 13px;
  margin-right: 25px;
}

.board__view__header--content p {
  margin: 0;
}

.board__view__header--content p:first-child {
  margin-bottom: 5px;
}

.board__view--contents {
  margin-top: 20px;
  margin-left: 40px;
  min-height: 400px;
  margin-bottom: 80px;
}
