.editor__title--input {
  padding: 0;
  width: 100%;
  height: 42px;
  margin-bottom: 15px;
  outline: none;
  font-size: 30px;
  font-family: "Noto Sans KR", sans-serif;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  padding-bottom: 20px;
  font-weight: 500;
  resize: none;
  line-height: 40px;
  overflow: hidden;
  letter-spacing: -0.4px;
}

.wrapper__editor {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
}
