.manage {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 116px;
  flex-direction: column;
}

.manage__columns {
  position: relative;
  display: flex;
  margin-bottom: 20px;
}

.manage__column--main {
  background-color: #2064e5 !important;
}

.manage__column--main--title {
  color: white !important;
}
.manage__column--title {
  font-size: 27px;
  color: black;
  font-weight: bold;
}

.manage__column__count--btn {
  background-color: white !important;
  color: #2064e5 !important;
}

.manage__column__basic--btn {
  width: 111px;
  height: 40px;
  padding: 10px 9px;
  background-color: #2064e5;
  color: white;
  font-size: 12px;
  border: none;
  position: absolute;
  left: 60px;
  bottom: 60px;
  border-radius: 30px;
}

.manage__column--img {
  bottom: 0px;
  right: 00px;
  position: absolute;
}

.manage__column {
  margin-right: 20px;
  width: 360px;
  height: 255px;
  padding: 60px 20px 0px 60px;
  border-radius: 20px;
  background-color: #f6f7f8;
  position: relative;
}
