.template__wrapper {
  margin-left: 154px;
  width: 1362px;
}

.template__title {
  font-size: 18px;
  color: #001d36;
  font-weight: 500;
  margin-bottom: 30px;
}

.template__lists {
  width: 264px;
  height: 704px;
  border-top: 1px solid #d1d1d1;
  border-left: 1px solid #d1d1d1;
  border-bottom: 1px solid #d1d1d1;
  position: relative;
}

.template__boxes {
  display: flex;
  margin-bottom: 30px;
}

.template__lists--list {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  background-color: white;
  font-size: 12px;
  font-weight: normal;
  color: #9099a4;
  border-bottom: 1px solid #d1d1d1;
}

.template__lists--list--selected {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 12px;
  font-weight: normal;
  background-color: #2064e5;
  color: white;
}

.template__lists--addBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
  width: 224px;
  height: 32px;
  background-color: #2064e5;
  color: white;
  font-size: 14px;
  font-weight: normal;
  border-radius: 5px;
}

.template__contents {
  width: 1036px;
  height: 704px;
  border: 1px solid #1c5ae1;
  padding: 0 30px;
}

.template__contents__row {
  display: flex;
  align-items: center;
  padding-bottom: 30px;
  padding-top: 30px;
  border-bottom: 1px solid #e3e3e3;
}

.template__contents__row--title {
  min-width: 150px;
  font-size: 12px;
  font-weight: 500;
  color: #696d78;
}

.template__contents__row--input {
  width: 376px;
  height: 30px;
  border: 1px solid #cbcbcb;
  padding-left: 10px;
  outline: none;
}

.template__toggle--btn--selected {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 10px;
  width: 120px;
  height: 25px;
  border: 1px solid #1c5ae1;
  background-color: #1c5ae1;
  color: white;
}

.template__toggle--btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 10px;
  width: 120px;
  height: 25px;
  border: 1px solid #cbcbcb;
  background-color: white;
  color: #9099a4;
}

.template__action__box {
  margin-left: auto;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 50px;
}

.template__createBtn {
  display: flex;
  justify-content: center;
  border-radius: 5px;
  align-items: center;
  width: 200px;
  height: 40px;
  background-color: #2064e5;
  color: white;
  font-size: 12px;
  font-weight: 500;
}

.template__deleteBtn {
  border: 1px solid #1c5ae1;
  color: #2064e5;
  background-color: white;
  display: flex;
  justify-content: center;
  border-radius: 5px;
  align-items: center;
  width: 200px;
  height: 40px;
  font-size: 12px;
  font-weight: 500;
  margin-right: 10px;
  cursor: pointer;
}

.template__contents__row--hidden {
  padding: 15px 20px;
  width: 100%;
  background-color: #f6f7f8;
}

.template__contents__row__inside {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.template__contents__row--endText {
  font-size: 12px;
  font-weight: 500;
  color: #696d78;
  margin-left: 8px;
}

.template__contents__row--textarea {
  width: 600px;
}

.template__contents__update--img {
  width: 88px;
  height: 88px;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.template__openImageButton {
  height: 34px;
  background-color: #9099a4;
  border: none;
  color: white;
  font-size: 12px;
}
