.side__wrapper {
  display: flex;
  height: 100%;
}
.sidebar {
  background-color: #f9fcff;
  min-width: 313px;
  display: flex;
  flex-direction: column;
  top: 0;
  padding-top: 54px;
  padding-left: 50px;
  border-right: 1px solid #d1d1d1;
}

.sidebar__column {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}

.manageMainCog {
  /*color: #a9a5a1;*/
}

.sidebar__icon {
  font-size: 16px;
  margin-right: 15px;
}

.sidebar__icon--selected {
  color: #1954c2 !important;
}

.sidebar__title--selected {
  color: #1954c2 !important;
}

.sidebar__title {
  font-size: 18px;
  font-weight: 500;
  color: #a3bfdd;
}

.board__comment--input {
  width: 90%;
  height: 35px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  outline: none;
  padding: 1px 5px;
}

.board__comment--write {
  display: flex;
  width: 100%;
}
.board__comment--btn {
  font-size: 15px;
  font-weight: 600;
  height: 39px;

  padding: 8px 10px;
  border: none;
  color: white;
  background-color: gray;
}
