.payment {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.payment__title--box {
  padding-bottom: 50px;
  border-bottom: 1px solid #2064e5;
  width: 95%;
}

.payment__title {
  font-size: 38px;
  font-weight: bold;
}

.payment__main--box {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
}

.payment__main--alert {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 40px;
}

.payment__sub--box {
  display: flex;
  width: 300px;
  border-radius: 5px;
  flex-direction: column;
  border: 1px solid #e3e3e3;
  margin-right: 23px;
  height: 446px;
  position: relative;
}

.payment__sub--monthly {
  background-color: #f6f7f8;
  border-bottom: 1px solid #e3e3e3;
  height: 10px;
  padding: 15px 20px;
  font-size: 15px;
  font-weight: 600;
}

.payment__sub--money {
  display: flex;
  justify-content: center;
  padding-top: 54px;
  /* padding-bottom: 54px;
  border-bottom: 1px solid #e3e3e3; */
  font-size: 40px;
  font-weight: 600;
  align-items: center;
}

.payment__sub__boxes {
  display: flex;
}

.payment__sub--dc {
  display: flex;
  justify-content: center;
  padding-top: 15px;
  font-size: 30px;
  font-weight: 500;
  color: #696d78;
  padding-bottom: 54px;
  border-bottom: 1px solid #e3e3e3;
}

.payment__sub--money .won {
  font-size: 15px;
  color: #696d78;
  font-weight: 500;
  align-self: flex-end;
  margin-left: 5px;
}

.payment__sub--contents {
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.payment__contents--text {
  margin-bottom: 7px;
  color: #9099a4;
}

.payment__sub__footer {
  display: flex;
  justify-content: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10px;
}

.payButton {
  border-radius: 5px;
  padding: 12px 0px;
  width: 260px;
  border: 1px solid #2182ff;
  color: #2064e5;
  font-weight: bold;
  background-color: white;
}

.visNoneWhite {
  color: white;
}

.payment__sub__monthly--blue {
  background-color: #2064e5 !important;
  color: white;
}

.payment__sub__box--blue {
  border: 1px solid #2064e5;
}

.payButton--blue {
  background-color: #2064e5;
  color: white;
}

.innerTable {
  padding-left: 88px;
}

.innerTable table tr:first-child {
  background-color: #f6f7f8;
}

.innerTable table {
  border: 1px solid #cbcbcb;
  border-spacing: 0 !important;
}

.innerTable table tr th {
  padding: 15px 40px;
  border: 1px solid #cbcbcb;
}

.innerTable table tr td {
  border: 1px solid #cbcbcb;
  padding: 15px;
}

.innerTable__input {
  border: 1px solid #cbcbcb;
  background-color: white;
  height: 25px;
  border-radius: 5px;
  outline: none;
}

.addNewOptionBtn {
  margin-left: auto;
  border-radius: 20px;
  display: none;
}

.disNone {
  display: none !important;
}
