.admin__main__wrapper {
  margin-top: 60px;
  margin-left: 155px;
}

.admin__manage--boxes {
  display: flex;
  margin-bottom: 20px;
}

.admin__manage__box {
  width: 640px;
  height: 419px;
  background-color: #f6f7f8;
  border-radius: 15px;
  padding-top: 20px;
  padding-left: 30px;
  background-image: url(../images/sample_graph.png);
  background-position: bottom;
  background-repeat: no-repeat;
}

.no-bgImg {
  background-image: none !important;
}

.admin__members {
  display: flex;
  flex-wrap: wrap;
}

.admin__manage__box:first-child {
  margin-right: 20px;
}

.admin__manage__subtitle {
  font-size: 18px;
  font-weight: 500;
}

.admin__member--nickname {
  margin-top: 15px;
  margin-bottom: 15px;
  color: #001d36;
  font-size: 16px;
  font-weight: 500;
}

.admin__member--email {
  margin: 0;
  font-size: 12px;
  color: #9099a4;
  margin-bottom: 15px;
}

.admin__manage__count {
  font-size: 50px;
  font-weight: 500;
  margin-top: 0;
}

.admin__members--profile {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 40px;
}
