.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.header {
  padding: 16px 40px 16px 40px;
  display: flex;
  justify-content: flex-end;
  border-bottom: 1px solid #e3e3e3;
  margin-bottom: 90px;
}

.header__login--btn {
  width: 120px;
  height: 32px;
  font-size: 12px;
  background-color: #2064e5;
  color: white;
  border-radius: 4px;
  border: none;
  margin-right: 10px;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main__title {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  font-size: 58px;
  color: #2064e5;
  cursor: pointer;
  margin-bottom: 40px;
}

.main__images--img {
  width: 460px;
  height: 270px;
  object-fit: cover;
}

.main__images--img:nth-child(2n) {
  margin-left: 20px;
}

.main__images__box {
  margin-bottom: 20px;
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 3px;
  width: 508px;
  height: 363px;
  background-color: white;
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: 0.3s;
  z-index: 100;
  padding-bottom: 30px;

  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.25);
}

.modal__title {
  padding-bottom: 20px;
  font-size: 18px;
  font-weight: 500;
  color: #001d36;
  padding-top: 25px;
}

.modal__login--btn {
  margin-bottom: 8px;
  width: 360px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.modal__button--naver {
  background-image: url(../images/naver_login.png);
}

.modal__button--facebook {
  background-image: url(../images/facebook_login.png);
  border: none;
}

.modal__button--kakao {
  background-image: url(../images/kakao_login.png);
}

.modal__button--text {
  color: white;
  font-size: 16px;
  font-weight: 500;
}

.modal__login--cancel,
.modal__remove--btn {
  width: 200px;
  height: 40px;
  border: 1px solid #7588ab;
  background-color: white;
  border-radius: 4px;
  color: #7588ab;
  cursor: pointer;
  margin-top: 25px;
  margin-right: 10px;
}

.modal__remove--btn {
  background-color: #2064e5;
  color: white;
  font-weight: bold;
}

.overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  cursor: pointer;
}

.modal__linkbox {
  margin-top: 30px;
  display: flex;
  width: 420px;
}

.modal__link--text {
  font-size: 12px;
  font-weight: 500;
  color: #696d78;
  margin: 9px 8px 9px 0;
}

.modal__link--input {
  width: 220px;
  height: 30px;
  border: 1px solid #cbcbcb;
  margin: 0 0 0 8px;
  outline: none;
}

.browse--btn {
  width: 80px;
  height: 34px;
  padding: 9px 18px;
  background-color: #9099a4;
  color: white;
  font-size: 10px;
  border: none;
}

.imageUploadPopup__image {
  width: 420px;
  height: 240px;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.db__dialog {
  width: 90%;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
  border-collapse: separate;
  border-spacing: 0 15px;
}

.db__dialog--head {
  height: 47px;
  background-color: #f6f7f8;
  border-top: 1px solid #2064e5;
  border-bottom: 1px solid #2064e5;
}
.dialog__header {
  border-top: 1px solid #2064e5;
  border-bottom: 1px solid #2064e5;
}

.dialog__column {
  display: flex;
  align-items: center;
}

.db__dialog--content {
  height: 47px;
  padding: 15px 0px;
  border-bottom: 1px solid #e3e3e3;
}

.db__dialog--content input[type="text"] {
  outline: none;
  border: 1px solid #cbcbcb;
  height: 30px;
}

.db__dialog--content .won {
  margin-left: 5px;
  font-size: 12px;
  font-weight: normal;
  color: #696d78;
}

@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
