.lists {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 50px;
}

.lists__title {
  color: #001d36;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 30px;
  align-self: flex-start;
}

.productList {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 100%;
  margin-bottom: 25px;
}

.productList th {
  border-top: 1px solid #2064e5;
  border-bottom: 1px solid #2064e5;
  padding: 18px 0px;
  background-color: #f6f7f8;
  font-size: 12px;
  font-weight: 500;
}

.productList td {
  font-size: 12px;
  color: #696d78;
  border-bottom: 1px solid #e3e3e3;
}

.productList td img {
  vertical-align: top !important;
}

.productList td {
  padding-top: 9px;
  vertical-align: middle;
  padding-bottom: 9px;
}

.lists_pagination {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 20px;
  justify-content: center;
}

.pagination__assets {
  width: 30px;
  height: 30px;
}

.lists__previousButton {
  margin-right: 10px;
}

.pageButton {
  color: #d1d1d1;
  border: 1px solid #d1d1d1;
  display: flex;
  padding: 6px;
  align-items: center;
  font-size: 18px;
  font-weight: 500;
  margin-right: 10px;
}

.pageButton--selected {
  background-color: #2064e5;
  color: white;
}

.lists__buttonBox {
  display: flex;
  margin-left: auto;
}

.lists__processButton {
  align-self: flex-end;
  background-color: #2064e5;
  color: white;
  font-size: 15px;
  font-weight: 600;
  padding: 8px 10px;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-right: 10px;
}
