.boardNavWrapper {
  width: 100%;
  height: 100%;
  display: block;
}

.boardNav {
  margin-top: 30px;
  display: flex;
  padding-left: 154px;
  border-bottom: 1px solid #d1d1d1;
  height: 46px;
  margin-bottom: 51px;
}

.boardTable__head {
  background-color: #f6f7f8;
  height: 47px;
  border-top: 1px solid #2064e5;
  border-bottom: 1px solid #2064e5;
  font-size: 12px;
  font-weight: 500;
  color: black;
}

.boardTable {
  padding-left: 154px;
  margin-bottom: 55px;
}

.boardNav__box {
  width: 244px;
  text-align: center;
}

.nav__item--default {
  font-size: 18px;
  font-weight: 500;
  padding-bottom: 12px;
  color: #9099a4;
  border-bottom: 3px solid #d1d1d1;
}

.nav__item--selected {
  color: #001d36;
  border-bottom: 3px solid #001d36;
}

.board__wrapper {
  width: 1300px;
  text-align: center;
}

.boardTable__body {
  height: 67px;
  font-size: 12px;
  font-weight: normal;
  color: #696d78;
  border-bottom: 1px solid #e3e3e3;
}
