.ads__wrapper {
  margin-top: 60px;
  margin-left: 154px;
}

.ads__image__boxes div {
  width: 640px;
  height: 365px;
}

.ads__image__boxes {
  display: flex;
  margin-bottom: 20px;
}

.ads__image__boxes div:first-child {
  margin-right: 20px;
}

.ads__image {
  width: 640px;
  height: 365px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.ads__btn--save {
  margin-top: 50px;
  margin-bottom: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 40px;
  border-radius: 4px;
  background-color: #2064e4;
  color: white;
  margin-left: auto;
}
